body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h3 {
  margin-top:50px;
}
.hero {
    height: 300px;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  .hero::after {
    content: "";
    display: block;
    opacity: .4;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: black;
  }
  
  .hero * {
    position: relative;
    z-index: 2;
  }
  
  .hero h1, .hero h2 {
    color: white;
  }
  
  .hero h1 {
    font-size: 60px;
    padding-top: 50px;
    margin-top: 0;
  }

  @media only screen and (min-width: 768px) {
    .hero h1 {
      padding-top: 100px;
    }
  }
form.search {
    overflow: auto;
    margin-bottom: 10px;
  }
  
  form.search button[type="submit"] {
    float: right;
    margin-top: 10px;
  }
.delete-btn {
    float: right;
    color: #e74944;
  }
  
nav.navbar.navbar-default {
    margin-bottom: 0;
}

li.nav-item a {
    color: rgba(0,0,0,.6);
}
li.nav-item a:hover {
    color: rgba(0,0,0,.95);
    text-decoration:none;
}

li.nav-item a:active,
li.nav-item a:focus {
    color: rgba(0,0,0,.95);
}

li.nav-item a:first-child {
    margin-right: 15px;
}

.navbar-brand a {
    margin-right: 15px;
    color: rgba(0,0,0,.9);
}

.navbar-brand a:hover {
    text-decoration:none;
}
.footer {
    min-height: 100px;
    background: #E5E5E5;
    position: relative;
  }
  
  .footer span {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 3px;
    background: #B8B8B8;
  }
.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

